import checkBMap from "../utils/checkMap.js";
import bindContextMenu from '../utils/bindContextMenu.js';

const PROPS = {
    // 
    visible: {
        required: false,
        type: Boolean,
        twoway: false,
        default: true
    },
    // 
    contextMenu: {
        required: false,
        type: Array,
        twoway: false,
        default: undefined
    },
    // ()id
    cid: {
        required: false,
        type: String,
        twoway: false
    }
};

export default {
    props: PROPS,
    data: function () {
        return {
            componentType: "without declare",
            created: false,                         // [] createComponent  true
            $map: undefined,                        // 
            mapObj: undefined,                      // 
            mapComponentObj: undefined,             // 
            mapContextMenu: undefined               // 
        }
    },
    created: function () {
        if ( checkBMap() ) {
            //  map.vue 
            this.$dispatch( "vue-baidu-map-register-component", this );
        } else {
            // TODO
        }
    },
    ready: function () {},
    detached: function () {
        this.beforeDeleteComponent();
    },
    destroyed: function () {
        this.beforeDeleteComponent();
    },
    watch: {
        "contextMenu": {
            handler: function ( val ) {
                bindContextMenu( this, this.mapComponentObj );
            },
            deep: true
        }
    },
    methods: {
        beforeCreateComponent: function () {
            this.created = true;
            this.createComponent();
        },
        createComponent: function () {
            // TODO: 
        },
        beforeDeleteComponent: function () {
            this.created = false;
            /**  */
            if ( this.mapComponentObj ) {
                // if ( this.mapContextMenu ) {
                //     this.mapComponentObj.removeContextMenu( this.mapContextMenu );
                // }
                this.deleteComponent();
            }
        },
        deleteComponent: function () {
            // TODO: 
        }
    },
    events: {
        "vue-baidu-map-ready": function ( $map ) {
            this.$map = $map;
            this.mapObj = this.$map.$data.mapObj;
            if ( this.mapObj ) {
                this.beforeCreateComponent();
            } else {
                console.error( "[vue-baidu-map] mapObj " );
            }
        }
    }
}


